import React from 'react';
import photo1 from '../assets/img/pic00.jpg';
import heart from '../assets/img/heart.png';
import { motion, useAnimationControls } from 'framer-motion';

function Home() {
    const variants = {
        hidden: {
            rotate: 15
        },
        visible: {
            opacity: 1,
            rotate: -15,
            transition: {
                delay: 0.2,
                duration: 0.5,
                repeat: Infinity,
                repeatType: 'reverse'
            }
        }
    };
    return (
        <div className="content home">
            <div className="name">
                <div>THE NEW BEGINNING</div>
                <div className="heart">
                    <span className="tag">
                        
                    </span>
                    <span className="tag dot">
                       
                    </span>
                    <span className="tag">
                        
                    </span>
                </div>
            </div>
            <div className="title1">
                <img src={photo1} />
            </div>
            <div className="location">
                <p>
                    2026 6 6   11 <br />
                      8
                </p>
            </div>
        </div>
    );
}

export default Home;
